import React, { useState, useEffect } from "react";
import img1 from "./img/landing1.png";
import {
  Navbar,
  Nav,
  Container,
  Button,
  Row,
  Col,
  Carousel,
  Card,
  Accordion,
  Image,
  Form,
} from "react-bootstrap";
import { FaSignInAlt, FaUserPlus } from "react-icons/fa";
import { motion, AnimatePresence } from "framer-motion";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.bundle.min.js";
import circleImg from "./img/circleLogo.jpg";
import {
  FaWhatsapp,
  FaMapMarkerAlt,
  FaRegCommentDots,
  FaPhone,
  FaEnvelope,
  FaArrowUp,
} from "react-icons/fa";
import { FaAngleLeft, FaAngleRight } from "react-icons/fa";
import { FaChevronDown } from "react-icons/fa";
import { Link, useNavigate } from "react-router-dom";
import transparentLogo from "./img/image.png";
import carousel1 from "./img/carousel1.png";

const Header = () => {
  const [showDropdown, setShowDropdown] = useState(null);
  const handleMouseEnter = (menu) => setShowDropdown(menu);
  const handleMouseLeave = () => setShowDropdown(null);
  const navigate = useNavigate();

  return (
    <Navbar
      expand="lg"
      sticky="top"
      className="shadow-sm"
      style={{
        background: "#2e3192",
        borderBottom: "1px solid rgba(255,255,255,0.2)",
        zIndex: 2000,
        minHeight: "70px", // reduced height
      }}
    >
      <Container
        fluid
        className="px-4 d-flex align-items-center justify-content-between"
        style={{ minHeight: "70px" }}
      >
        {/* LOGO */}
        <Navbar.Brand as={Link} to="#" className="d-flex align-items-center">
          <img
            src={transparentLogo}
            alt="Bank Mudra"
            style={{
              width: "200px",
              height: "70PX",
              objectFit: "contain",
              transition: "transform 0.3s ease",
              mixBlendMode: "color-burn",
            }}
            onMouseEnter={(e) =>
              (e.currentTarget.style.transform = "scale(1.05)")
            }
            onMouseLeave={(e) => (e.currentTarget.style.transform = "scale(1)")}
          />
        </Navbar.Brand>

        <Navbar.Toggle aria-controls="navbarResponsive" className="bg-white" />
        <Navbar.Collapse
          id="navbarResponsive"
          className="rounded p-lg-0 p-3 text-white"
        >
          {/* NAV LINKS */}
          <Nav className="mx-auto fw-semibold text-capitalize align-items-lg-center flex-column flex-lg-row gap-3">
            {[
              { key: "personal", label: "Personal" },
              { key: "merchant", label: "Merchant" },
              { key: "business", label: "Business" },
              { key: "investors", label: "Financier & Investors" },
              { key: "support", label: "Support" },
            ].map((menu) => (
              <div
                key={menu.key}
                className="position-relative"
                onMouseEnter={() =>
                  window.innerWidth > 991 && handleMouseEnter(menu.key)
                }
                onMouseLeave={() =>
                  window.innerWidth > 991 && handleMouseLeave()
                }
              >
                <span
                  className="nav-link d-flex align-items-center gap-1 justify-content-between"
                  onClick={() =>
                    window.innerWidth <= 991 &&
                    setShowDropdown(showDropdown === menu.key ? null : menu.key)
                  }
                  style={{
                    cursor: "pointer",
                    fontSize: "0.95rem",
                    color: "#fff",
                    padding: "4px 0", // slightly reduced vertical padding
                  }}
                >
                  {menu.label} <FaChevronDown size={12} />
                </span>

                {/* DROPDOWN */}
                {showDropdown === menu.key && (
                  <div
                    className="dropdown-menu show shadow-sm border-0 mt-2 p-3 animate-dropdown"
                    style={{
                      background: "#1b6dc1",
                      borderRadius: "12px",
                      position: window.innerWidth > 991 ? "absolute" : "static",
                      minWidth: window.innerWidth > 991 ? "600px" : "100%",
                    }}
                  >
                    <h6
                      className="fw-bold text-white mb-2"
                      style={{
                        fontSize: "0.9rem",
                        borderBottom: "1px solid rgba(255,255,255,0.2)",
                        paddingBottom: "4px",
                      }}
                    >
                      {menu.label} Services
                    </h6>
                    <ul className="list-unstyled d-flex flex-wrap gap-2 mb-0">
                      {["Overview", "Features", "Benefits", "Learn More"].map(
                        (item) => (
                          <li key={item}>
                            <Link to="#" className="dropdown-item-modern">
                              {item}
                            </Link>
                          </li>
                        )
                      )}
                    </ul>
                  </div>
                )}
              </div>
            ))}
          </Nav>

          {/* LOGIN / SIGN UP */}
          <div className="d-flex flex-lg-row flex-column align-items-center gap-2 mt-3 mt-lg-0">
            {/* LOGIN BUTTON */}
            <Button
              variant="outline-light"
              className="rounded-pill px-3 py-1 fw-semibold w-100 w-lg-auto d-flex align-items-center justify-content-center gap-2"
              onClick={() => navigate("/login-area")}
              style={{
                borderColor: "#fff",
                color: "#fff",
                fontSize: "0.9rem",
                transition: "all 0.3s ease",
              }}
              onMouseEnter={(e) => {
                e.currentTarget.style.background = "#fff";
                e.currentTarget.style.color = "#2e3192";
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.background = "transparent";
                e.currentTarget.style.color = "#fff";
              }}
            >
              <FaSignInAlt size={14} /> Login
            </Button>

            {/* SIGNUP BUTTON */}
            <Button
              variant="light"
              className="rounded-pill px-3 py-1 fw-semibold w-100 w-lg-auto d-flex align-items-center justify-content-center gap-2"
              onClick={() => navigate("/Signup")}
              style={{
                background: "#fff",
                color: "#2e3192",
                border: "none",
                fontSize: "0.9rem",
                transition: "transform 0.2s ease",
              }}
              onMouseEnter={(e) =>
                (e.currentTarget.style.transform = "translateY(-2px)")
              }
              onMouseLeave={(e) =>
                (e.currentTarget.style.transform = "translateY(0)")
              }
            >
              <FaUserPlus size={14} /> SignUp
            </Button>
          </div>
        </Navbar.Collapse>
      </Container>

      <style>{`
        .dropdown-item-modern {
          display: block;
          padding: 6px 10px;
          border-radius: 6px;
          font-size: 0.85rem;
          color: #fff;
          text-decoration: none;
          transition: all 0.25s ease;
        }
        .dropdown-item-modern:hover {
          color: #2e3192;
          background: #fff;
          transform: translateX(3px);
        }
        .animate-dropdown {
          animation: fadeSlide 0.25s ease;
        }
        @keyframes fadeSlide {
          from { opacity: 0; transform: translateY(-5px); }
          to { opacity: 1; transform: translateY(0); }
        }
        @media (max-width: 991px) {
          .dropdown-menu {
            box-shadow: none !important;
            padding: 0 !important;
            margin: 0 !important;
          }
        }
      `}</style>
    </Navbar>
  );
};

const MainSlider = () => {
  const carouselImages = [
    { url: carousel1 },
    { url: carousel1 },
    { url: carousel1 },
  ];

  const [activeIndex, setActiveIndex] = useState(0);

  const handlePrev = () =>
    setActiveIndex((prev) =>
      prev === 0 ? carouselImages.length - 1 : prev - 1
    );

  const handleNext = () =>
    setActiveIndex((prev) =>
      prev === carouselImages.length - 1 ? 0 : prev + 1
    );

  const handleSelect = (index) => setActiveIndex(index);

  return (
    <div
      className="relative text-center overflow-hidden rounded-4 shadow-lg"
      style={{ position: "relative" }}
    >
      <AnimatePresence mode="wait">
        <motion.div
          key={activeIndex}
          initial={{ opacity: 0, scale: 1.05, x: 40 }}
          animate={{ opacity: 1, scale: 1, x: 0 }}
          exit={{ opacity: 0, scale: 1.05, x: -40 }}
          transition={{ duration: 0.8, ease: "easeOut" }}
          className="position-relative main-slide"
        >
          {/* Background Image */}
          <img
            className="d-block w-100"
            src={carouselImages[activeIndex].url}
            alt={`Slide ${activeIndex + 1}`}
            style={{
              height: "100%",
              width: "100%",
              borderRadius: "20px",
              filter: "brightness(0.9)",
              userSelect: "none",
              pointerEvents: "auto",
              touchAction: "manipulation",
            }}
          />

          {/* Overlay gradient */}
          <div
            style={{
              position: "absolute",
              top: 0,
              left: 0,
              width: "100%",
              height: "100%",
              borderRadius: "20px",
              background:
                "linear-gradient(180deg, rgba(0,0,0,0.15) 0%, rgba(0,0,0,0.65) 100%)",
              pointerEvents: "none",
            }}
          />
        </motion.div>
      </AnimatePresence>

      {/* Navigation Buttons */}
      <div
        className="position-absolute top-50 start-0 end-0 d-flex justify-content-between align-items-center px-3"
        style={{
          transform: "translateY(-50%)",
          zIndex: 20,
          pointerEvents: "none",
        }}
      >
        <motion.button
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
          onClick={handlePrev}
          className="rounded-circle bg-white bg-opacity-75 border-0 shadow d-flex align-items-center justify-content-center"
          style={{
            width: "45px",
            height: "45px",
            pointerEvents: "auto",
            touchAction: "manipulation",
          }}
        >
          <FaAngleLeft className="fs-4 text-primary" />
        </motion.button>

        <motion.button
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
          onClick={handleNext}
          className="rounded-circle bg-white bg-opacity-75 border-0 shadow d-flex align-items-center justify-content-center"
          style={{
            width: "45px",
            height: "45px",
            pointerEvents: "auto",
            touchAction: "manipulation",
          }}
        >
          <FaAngleRight className="fs-4 text-primary" />
        </motion.button>
      </div>

      {/* Dot Indicators */}
      <div
        className="position-absolute bottom-3 start-50 translate-middle-x"
        style={{ zIndex: 20 }}
      >
        <ul className="list-unstyled d-flex gap-2 justify-content-center m-0 p-0">
          {carouselImages.map((_, index) => (
            <motion.li key={index}>
              <motion.button
                whileHover={{ scale: 1.2 }}
                whileTap={{ scale: 0.9 }}
                onClick={() => handleSelect(index)}
                className={`rounded-circle border-0 ${
                  index === activeIndex ? "bg-primary" : "bg-light"
                }`}
                style={{
                  width: index === activeIndex ? "14px" : "10px",
                  height: index === activeIndex ? "14px" : "10px",
                  opacity: index === activeIndex ? 1 : 0.5,
                  transition: "all 0.3s ease",
                  boxShadow:
                    index === activeIndex
                      ? "0 0 6px rgba(0, 0, 0, 0.3)"
                      : "none",
                  pointerEvents: "auto",
                }}
              ></motion.button>
            </motion.li>
          ))}
        </ul>
      </div>

      {/* Responsive CSS */}
      <style>{`
        @media (max-width: 768px) {
          img.d-block.w-100 {
            height: 45vh !important;
          }
          .rounded-circle {
            width: 38px !important;
            height: 38px !important;
          }
        }
        @media (max-width: 480px) {
          img.d-block.w-100 {
            height: 35vh !important;
          }
        }

         .main-slide {
    height: 70vh; /* Desktop default */
    width: 100%;
  }

  @media (max-width: 768px) {
    .main-slide {
      height: auto !important; /* Allow natural image height */
    }
  }

  @media (max-width: 480px) {
    .main-slide {
      height: auto !important;
    }
  }


      `}</style>
    </div>
  );
};

